import MagnifyIcon from 'vue-material-design-icons/Magnify.vue';
import CloseIcon from 'vue-material-design-icons/Close.vue';
import NcLoadingIcon from '@nextcloud/vue/dist/Components/NcLoadingIcon.js';
import NcEmptyContent from '@nextcloud/vue/dist/Components/NcEmptyContent.js';
import NcTextField from '@nextcloud/vue/dist/Components/NcTextField.js';
import PickerResult from '../components/PickerResult.vue';
import GetCapsDialog from '../components/GetCapsDialog.vue';
import axios from '@nextcloud/axios';
import { generateUrl, imagePath } from '@nextcloud/router';
import { delay } from '../utils.js';
import InfiniteLoading from 'vue-infinite-loading';
import Tooltip from '@nextcloud/vue/dist/Directives/Tooltip.js';
import Vue from 'vue';
Vue.directive('tooltip', Tooltip);
const LIMIT = 20;
export default {
  name: 'MemeCustomPickerElement',
  components: {
    PickerResult,
    GetCapsDialog,
    NcLoadingIcon,
    InfiniteLoading,
    NcEmptyContent,
    NcTextField,
    MagnifyIcon,
    CloseIcon
  },
  props: {
    providerId: {
      type: String,
      required: true
    },
    accessible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      searchQuery: '',
      searching: false,
      memes: [],
      selectedMemeId: null,
      inputPlaceholder: t('memegen', 'Search memes'),
      offset: null,
      abortController: null,
      poweredByImgSrc: imagePath('memegen', 'memegen.logo.png'),
      poweredByTitle: t('memegen', 'Powered by Memegen.link'),
      LIMIT,
      sadGifUrl: imagePath('memegen', 'sad.gif')
    };
  },
  computed: {},
  watch: {},
  mounted() {
    this.search();
    this.focusOnInput();
  },
  beforeDestroy() {
    this.cancelSearchRequests();
  },
  methods: {
    focusOnInput() {
      setTimeout(() => {
        // this.$refs['memegen-search-input']?.focus()
        this.$refs['memegen-search-input'].$el.getElementsByTagName('input')[0]?.focus();
      }, 300);
    },
    onSelect(meme) {
      this.cancelSearchRequests();
      this.selectedMemeId = meme.memeId;
      this.$refs['caps-dialog'].showCapsDialog(meme);
      // this.$emit('submit', meme.resourceUrl)
    },

    onSubmit(extUrl) {
      this.$emit('submit', extUrl);
    },
    onInput() {
      delay(() => {
        this.updateSearch();
      }, 500)();
    },
    onClear() {
      this.searchQuery = '';
      this.updateSearch();
    },
    updateSearch() {
      if (this.$refs.results?.scrollTop) {
        this.$refs.results.scrollTop = 0;
      }
      this.cancelSearchRequests();
      this.memes = [];
      this.offset = 0;
      this.search();
    },
    cancelSearchRequests() {
      if (this.abortController) {
        this.abortController.abort();
      }
    },
    infiniteHandler($state) {
      this.search($state);
    },
    search(state = null, limit = LIMIT) {
      this.abortController = new AbortController();
      this.searching = true;
      const url = this.offset === null ? generateUrl('apps/memegen/search?term={term}&limit={limit}', {
        term: this.searchQuery,
        limit
      }) : generateUrl('apps/memegen/search?term={term}&offset={offset}&limit={limit}', {
        term: this.searchQuery,
        offset: this.offset,
        limit
      });
      return axios.get(url, {
        signal: this.abortController.signal
      }).then(response => {
        this.offset = response.data.offset;
        this.memes.push(...response.data.entries);
        if (state !== null) {
          if (response.data.entries.length > 0) {
            state.loaded();
          }
          if (response.data.entries.length < limit) {
            state.complete();
          }
        }
      }).catch(error => {
        console.debug('memegen search request error', error);
        if (state !== null) {
          state.complete();
        }
      }).then(() => {
        this.searching = false;
      });
    }
  }
};